import styled from "styled-components";
import { Title3, Title4 } from "../../assets/styles/component";
import Button from "../../assets/UI/ButtonDesktop";
import { css } from "styled-components";
import { EngLoc } from "../../assets/constants/localizations";
import { VideoBtn } from "../../assets/images/Elements/VideoBtn";
import { useState } from "react";

const SHorizontalBox = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 5rem;
  grid-column: 1/3;

  overflow: hidden;

  box-sizing: border-box;
  width: 100%;
  max-width: 1700px;
  min-height: 40rem;
  padding: 2.813rem;

  @media (max-width: 1500px) {
    padding: 2.188rem;
    border-radius: 2.063rem;
  }

  text-align: left;

  border-radius: 3.313rem;
  background: ${({ color }) => {
    switch (color) {
      case "blue":
        return `
          radial-gradient(68.55% 82.85% at 11.32% 115.16%, #36DBFF 0%, rgba(54, 219, 255, 0) 100%), #2D85F7;),
          #2d84f7`;

      case "green":
        return `
          radial-gradient(68.55% 82.85% at 11.32% 115.16%,#48DAFC 0%, rgba(54, 219, 255, 0) 100%), 
          #10ACE0;`;

      case "pink":
        return `
          radial-gradient(51.72% 161.8% at 97.16% -41.48%,#682A77 0%, rgba(104, 42, 119, 0) 100%), 
          radial-gradient(54.77% 56.72% at 11.32% 115.16%, #B13F67 0%, rgba(177, 63, 105, 0) 100%), 
          #2B2257;`;

      case "darkblue":
        return `
          radial-gradient(101.6% 186.88% at 97.16% -41.48%,#0C0A5D 0%, rgba(13, 10, 94, 0) 100%), 
          radial-gradient(54.77% 56.72% at 11.32% 115.16%, #6C2EDA 0%, rgba(108, 46, 218, 0) 100%), 
          #050347;`;

      case "darkviolet":
        return `
          radial-gradient(48.5% 50.08% at 31.84% 78.44%, rgba(142, 71, 236, 0.2) 0%, rgba(0, 0, 0, 0) 100%, 
          rgba(142, 71, 236, 0) 100%), 
          radial-gradient(101.6% 186.88% at 97.16% -41.48%, #8E47EC 0%, rgba(13, 10, 94, 0) 100%), 
          radial-gradient(49.08% 61.64% at 1.78% 133.91%, #DFA25F 0%, rgba(223, 162, 95, 0) 100%), 
          #1F1F1F;`;
    }
  }};
`;

const Desc = styled.div`
  max-width: 40.875rem;
  width: 40%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 1.875rem;

  @media (max-width: 1300px) {
    max-width: 28.375rem;
  }
`;

const Label = styled.div`
  display: flex;
  flex-direction: column;
  gap: 1.25rem;
`;

const Name = styled(Title3)`
  font-weight: 400;
  font-size: 3.438rem;
  line-height: 110%;
`;

const textStyle = css`
  font-weight: 400;
  font-size: 1.125rem;
  line-height: 150%;
`;

const Title = styled(Title4)`
  font-weight: 400;
  font-size: 1.625rem;
  line-height: 120%;
`;

const List = styled.ul`
  padding-left: 1.875rem;
  max-width: 547px;
  color: white;
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 1.25rem;

  & li {
    position: relative;
    ${textStyle}
  }

  & li::before {
    content: "";
    background-color: #ffffff55; /* Цвет маркера */
    width: 0.625rem;
    height: 0.625rem;
    position: absolute;

    top: 0.625rem;
    left: -26px;
    border-radius: 100%;
  }
`;

const Socials = styled.div`
  display: flex;
  gap: 1.063rem;
  margin-top: 6px;

  & svg {
    opacity: 0.5;
    width: 1.875rem;
    height: 1.875rem;
  }
`;

const ButtonBox = styled.div`
  display: flex;
  gap: 0.625rem;

  & a {
    width: 284px;
    background-color: #ffffff;
    border-radius: 3.125rem;
    position: relative;
    overflow: hidden;

    &::after {
      content: "";
      position: absolute;
      bottom: -50%;
      left: -100%;
      width: 300px;
      height: 300px;
      background: radial-gradient(38.51% 96.45% at 50% 50%, #fcfdfe 0%, rgba(217, 217, 217, 0) 100%);
      transform: rotateZ(160deg);
    }

    &:hover::after {
      animation: sheen 2s forwards;
      animation-iteration-count: infinite;
      animation-fill-mode: forwards;
    }

    @keyframes sheen {
      100% {
        left: 110%;
      }
    }
  }
`;

const ProductView = styled.div`
  position: relative;

  display: flex;
  justify-content: flex-end;
  align-items: center;

  width: 100%;
  height: 100%;

  margin-right: 0.938rem;

  perspective: 1200px;
  max-width: 761px;
  transform-style: preserve-3d;
  cursor: pointer;

  & img {
    width: 100%;
    max-width: 47.563rem;
    max-height: 31.188rem;
    aspect-ratio: 761 / 499;

    margin-right: 0;
  }

  iframe {
    position: absolute;
    z-index: 1;
    border-radius: 30px;
    max-width: 47.563rem;
    max-height: 31.188rem;
  }

  ${({viewVideo = false}) => viewVideo && `
    & > div {
      animation: rotate_card 1s forwards;
      @keyframes rotate_card {
        0% {
          transform: rotateY(0);
        }
        100% {
          transform: rotateY(180deg);
        }
      }
    }`
  }
`;

const Flipper = styled.div`
  position: relative;
  transform-style: preserve-3d;
  transition: 0.7s linear;

  width: 100%;
  height: 100%;

  & > div {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    backface-visibility: hidden;
    display: flex;
    align-items: center;
  }
`;

const ViewFront = styled.div`
  z-index: 2;
  transform: rotateY(0deg);
`;

const ViewBack = styled.div`
  z-index: 1;
  transform: rotateY(-180deg);
`;

const PlayBtn = styled.button`
  position: absolute;
  top: calc(50% - 4vw);
  left: calc(50% - 4vw);

  display: flex;
  justify-content: center;
  align-items: center;

  background: #fff;
  border: 0;
  outline: 0;
  border-radius: 8.688rem;

  width: 8.688rem;
  height: 8.688rem;
  max-width: 8.688rem;
  max-height: 8.688rem;

  @media (max-width: 1550px) {
    top: calc(50% - 3.7vw);
    left: calc(50% - 3.7vw);
    width: 6.688rem;
    height: 6.688rem;
  }

  @media (max-width: 1200px) {
    width: 5.688rem;
    height: 5.688rem;
  }

  svg {
    margin-right: -5px;
  }
`;

function HorizontalBox({
  boxcolor,
  btncolor,
  name,
  title,
  view,
  list,
  socials,
  href,
  language,
  video,
}) {

  const [viewVideo, setViewVideo] = useState(false);

  return (
    <SHorizontalBox color={boxcolor}>
      <Desc>
        <Label>
          <Name>{name}</Name>
          <Title>{title}</Title>
          <List>
            {list &&
              list.map((list_item, index) => <li key={index}>{list_item}</li>)}
          </List>
        </Label>
        <Socials>{socials}</Socials>
        <ButtonBox>
          <a href={href} aria-label={"Узнать больше о продукте " + name}>
            <Button type="secondary" colorTxt={btncolor} link={true}>
              {language === "Ru" ? "Узнать больше" : EngLoc.buttons.more}
            </Button>
          </a>
        </ButtonBox>
      </Desc>
      <ProductView viewVideo={viewVideo} onClick={() => {setViewVideo(true)}}>
        <Flipper>
          <ViewFront>
            <PlayBtn>
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="37"
                height="37"
                viewBox="0 0 58 58"
                fill="none"
              >
                <path
                  d="M56.2808 27.3172L3.36708 0.8603C2.03728 0.195399 0.472656 1.16239 0.472656 2.64915V55.5629C0.472656 57.0496 2.03728 58.0166 3.36708 57.3517L56.2808 30.8949C57.7549 30.1578 57.7549 28.0542 56.2808 27.3172Z"
                  fill="#176AB2"
                />{" "}
              </svg>
            </PlayBtn>
            <img loading="lazy" src={view} alt="Product View" />
          </ViewFront>
          <ViewBack>
            <iframe
              width="100%"
              height="100%"
              src={video}
              title="YouTube video player"
              frameBorder="0"
              allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share"
              allowFullScreen
            ></iframe>
          </ViewBack>
        </Flipper>
      </ProductView>
    </SHorizontalBox>
  );
}

export default HorizontalBox;
